import React, { Component } from "react";
import axios from "axios";
import Container from '@mui/material/Container';
import Typography from "@mui/material/Typography";
import { Grid, Skeleton, Box } from "@mui/material";
import PageAboutInfoRItem from "./PageAboutInfoRItem";
import PageAboutInfoLItem from "./PageAboutInfoLItem";
import { styled } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import { Markup } from "interweave";

const ImageBox = styled(Box)(({ theme }) => ({
    position: 'relative',
    height: 500,
    [theme.breakpoints.down('sm')]: {
        width: '100% !important', // Overrides inline-style
        height: 200,
    },
}));

const ImageSrc = styled('img')({
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundSize: 'cover',
    backgroundPosition: 'center 40%',
});

const ContainerBox = styled(Stack)(({ theme }) => ({
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: theme.palette.common.white,
}));

const ImageBackdrop = styled('span')(({ theme }) => ({
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.palette.common.black,
    opacity: 0.4,
    transition: theme.transitions.create('opacity'),
}));

class AppPageAboutInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            aboutInfo: {}, isLoaded: false
        }
    }

    componentDidMount() {
        this.aboutInfoData().then(r => {
            this.setState({ isLoaded: true, aboutInfo: r.data });
        });
    }

    aboutInfoData = async () => {
        return await axios.get("/api/pageaboutinfo/");
    };

    render() {
        const { isLoaded, aboutInfo } = this.state;
        if (!isLoaded) {
            return (
                <Box
                    sx={{
                        width: '100%',
                    }}>
                    <Skeleton
                        className="rounded"
                        variant="rectangular"
                        width="100%"
                        height={400} />
                    <Skeleton />
                    <Skeleton />
                </Box>
            );
        } else {
            return (
                aboutInfo ?
                    <Grid container>
                        <Grid item xs={12} sm={12} md={12}>
                            <Box sx={{ display: 'flex', flexWrap: 'wrap', maxHeight: "500px", minWidth: 40, width: '100%' }}>
                                <ImageBox
                                    focusRipple
                                    style={{
                                        width: "100%",
                                    }}>
                                    <ImageSrc src={aboutInfo.image} />
                                    <ImageBackdrop className="MuiImageBackdrop-root" />
                                    <ContainerBox>
                                        {aboutInfo.title ? (
                                            <Typography variant="h2" >
                                                {aboutInfo.title}
                                            </Typography>
                                        ) : ('')}
                                        {aboutInfo.description ? (
                                            <Typography variant="h5">
                                                <Markup content={aboutInfo.description} />
                                            </Typography>
                                        ) : ('')}
                                    </ContainerBox>
                                </ImageBox>
                            </Box>
                        </Grid>
                        <Container component='div'>
                            <Grid item xs={12} sm={12} md={12}>
                                {aboutInfo.points.map((point, index) => {
                                    if (index % 2 === 0) {
                                        return (
                                            <PageAboutInfoRItem
                                                key={index}
                                                point={point}
                                            />
                                        )
                                    } else {
                                        return (
                                            <PageAboutInfoLItem key={index} point={point} className={index} />
                                        )
                                    }
                                })}
                            </Grid>
                        </Container>
                    </Grid>
                    : ''
            );
        }
    }
}

export default AppPageAboutInfo;